<script>
export default {
  name: 'Home',
  components: {},
  data() {
    return {
      eventList: {
        /*'liveGP': {
          title: '104 2ndTerm 3',
          text: '',
          type: 'liveGP',
          firstDay: [2024, 10, 11, 12, 0],
          lastDay: [2024, 10, 17, 3, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/appnews/detail/?p=2024-10-09-20-n47gypu0nb',
          img: '1042ndTerm3_logo'
        },*/
        'liveGP': {
          title: '104 2ndTerm 3',
          text: '',
          type: 'liveGP',
          firstDay: [2024, 10, 19, 12, 0],
          lastDay: [2024, 10, 24, 3, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/appnews/detail/?p=2024-10-18-10-de5bpp0jlv',
          img: '104 2ndTerm 3_logo'
        },
        '3rdLiveTour_': {
          title: '  3rd Live Tour TRY TRI UNITY!!!',
          text: 'with ',
          type: 'live',
          firstDay: [2024, 11, 16, 0, 0],
          lastDay: [2024, 11, 16, 23, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=TTUM',
          img: '3rdLiveTour_logo_'
        },
        '3rdLiveTour_DOLLCHESTRA': {
          title: '  3rd Live Tour TRY TRI UNITY!!!',
          text: 'with DOLLCHESTRA',
          type: 'live',
          firstDay: [2024, 11, 24, 0, 0],
          lastDay: [2024, 11, 24, 23, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=TTUD',
          img: '3rdLiveTour_logo_DOLLCHESTRA'
        },
        '3rdLiveTour_': {
          title: '  3rd Live Tour TRY TRI UNITY!!!',
          text: 'with ',
          type: 'live',
          firstDay: [2024, 12, 1, 0, 0],
          lastDay: [2024, 12, 1, 23, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=TTUC',
          img: '3rdLiveTour_logo_'
        },
        'Live&FanMeeting': {
          title: '& Live & Fan Meeting TRY TRY UNITY!!!',
          text: '',
          type: 'live',
          firstDay: [2024, 12, 21, 0, 0],
          lastDay: [2024, 12, 22, 23, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=TTY',
          img: 'live&fanMeeting_logo'
        },
        '3rdLiveTour': {
          title: '  3rd Live Tour TRI TRI UNITY!!!',
          text: '',
          type: 'live',
          firstDay: [2025, 1, 10, 0, 0],
          lastDay: [2025, 1, 11, 23, 59],
          url: 'https://www.lovelive-anime.jp/hasunosora/live-event/live_detail.php?p=TTU',
          img: '3rdLiveTour_logo'
        },
        'AsiaTour': {
          title: 'LoveLive! Series Asia Tour 2024  ',
          text: '',
          type: 'live',
          firstDay: [2025, 2, 1, 0, 0],
          lastDay: [2025, 2, 2, 23, 59],
          url: 'https://lovelive-anime.jp/special/live/live_detail.php?p=asiatour_2024',
          img: 'AsiaTour_kv'
        }
      },
      outputEventList: {}
    }
  },
  created() {
    for (const key in this.eventList) {
      const arr = this.countDown(key);

      if (arr.state !== 'after') {
        this.outputEventList[key] = this.eventList[key];
        this.outputEventList[key].state = arr.state;

        if (arr.state === 'prev') {
          this.outputEventList[key].count = {
            day: arr.day,
            time: arr.time
          };
        }
      }
    }
  },
  methods: {
    pageMove(movePageName) {
      this.$router.replace(movePageName);
      window.scrollTo(0, 0);
    },
    countDown(eventName) {
      const firstDay = new Date(this.eventList[eventName].firstDay[0], (this.eventList[eventName].firstDay[1] - 1), this.eventList[eventName].firstDay[2], this.eventList[eventName].firstDay[3], this.eventList[eventName].firstDay[4], 0);
      const lastDay = new Date(this.eventList[eventName].lastDay[0], (this.eventList[eventName].lastDay[1] - 1), this.eventList[eventName].lastDay[2], this.eventList[eventName].lastDay[3], this.eventList[eventName].lastDay[4], 59);
      const today = new Date();

      const f = new Date(firstDay.getFullYear(), firstDay.getMonth(), firstDay.getDate());
      const l = new Date(lastDay.getFullYear(), lastDay.getMonth(), lastDay.getDate());
      const t = new Date(today.getFullYear(), today.getMonth(), today.getDate());

      if (f.getTime() === t.getTime()) {
        if (today.getHours() < firstDay.getHours()) {
          return {
            state: 'prev',
            day: 0,
            time: firstDay.getHours() - today.getHours()
          };
        } else {
          return {
            state: 'now'
          };
        }
      } else if (f.getTime() > t.getTime()) {
        return {
          state: 'prev',
          day: -(Math.floor((today.getTime() - firstDay.getTime()) / (1000 * 60 * 60 * 24))),
          time: 0
        };
      } else if (t.getTime() < l.getTime()) {
        return {
          state: 'now'
        };
      } else if (l.getTime() === t.getTime()) {
        if (today.getTime() <= lastDay.getTime()) {
          return {
            state: 'now'
          };
        }
      }

      return {
        state: 'after'
      };
    }
  }
}
</script>

